export default {
    namespace: 'app',
    state: {
        _headTitle: "",//head
        _alertEmptySwitch:false,//
        _navArr: [{
            name: "",
            to: "/"
        },
            {
                name: "",
                to: "/kuaishou"
            },
            {
                name: "",
                to: "/huoshan"
            },
        ],//nav
    },
    reducers: {
        //headTitle
        "changeHeadTitle"(state: any, {payload:{headTitle}}: any) {
            return {
                ...state,
                _headTitle: headTitle,
            }
        },
        //
        "changeEmptySwitch"(state: any, {payload:{alertEmptySwitch}}: any) {
            return {
                ...state,
                _alertEmptySwitch: alertEmptySwitch,
            }
        }
    },
    effects: {}
}


